*, ::after,::before {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
body {
    padding: 0 20px;
    background-image: url(https://media0.giphy.com/media/l3vRmjIZpfYp8MLwA/200.webp?cid=ecf05e47zffowii5tspd0i6y4k25h8hzi42j28d9qreeykj1&rid=200.webp);
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center;
    color: white;
    
}
nav.navbar {
    height: 70px;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    list-style: none;
    background:rgba(0, 140, 255, 1);
    color:#fff;
    font-size: 28px;
    margin-top: 10px;
}
p {
    margin: 20px;
}

.App {
    display: flex;
    flex-direction: column;
    align-items: center;
}
.cookies {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding-top: 40px;
}
img {
    width: 200px;
    height: auto;

}
.container-middle {
    display: flex;
    
    align-items: center;
   
}
.cookie{
   
    background-position: center;
    text-align: center;
    border-radius:50%;
    width: 200px;
    height: 200px;
    background-image: url("https://external-content.duckduckgo.com/iu/?u=https%3A%2F%2Fimg.freepik.com%2Ffree-photo%2Fcookies-with-dark-chocolate-chips_1339-25695.jpg%3Fsize%3D626%26ext%3Djpg&f=1&nofb=1");
    background-size:cover;
    outline: none;
    color:black;
    font-size: 28px;
    font-weight: bold;
    margin: 0 100px;
}
.cookie:hover {
    font-size: 32px;
    text-decoration: underline;
}

.display{
    display: flex;
    justify-content: space-between;
    justify-content: center;
    width: 80%;
    height: 500px;
    margin: 25px auto;
    height: 600px;
    
}

.shop{
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    width: 25%;
    /* border:solid black 1px; */
    border-radius: 3%;
    margin: 10px;
    padding: 5px;
    font-size: 34px;
    font-weight: 900;
}

.totalCookies{
    display: flex;
    flex-direction: column;
    justify-content: center;
    
}

.displayCookie{
    text-align: center;
    width: 25%;
    /* border:solid black 1px; */
    border-radius: 3%;
    margin: 10px;
    padding: 5px;
    font-size: 34px;
    font-weight: 900;
}

.level{
    display: flex;
    justify-content: center;
    align-items: center;
    width: 110px;
    height: 110px;
    background-size: cover;
    
}

.level1{
    background-image: url(images/level_1.svg);
    background-position: center;
    margin-left: 25px;
}




.level2{
    background-image: url(images/level_2.svg);
    background-position: center;
    margin-left: 25px;
    margin-top: 50px;
}


.level3{
    background-image: url(images/level_3.svg);
    background-position: center;
    margin-left: 25px;
    margin-top: 50px;
}


.box{
    display: flex;
    flex-direction: column;
    text-align: center;
    width: 100%;
    margin: 10px;
    align-items: center;
}
